
.StripeElement--focus {
    box-shadow: rgba(50, 50, 93, 0.109804) 0px 4px 6px, rgba(0, 0, 0, 0.0784314) 0px 1px 3px !important;
}

.infoPageRoot {
    width: calc(100vw - 128px);
    display: flex;
    justify-content: center;
}

.infoPageCardRoot {
    width: 600px;
    background-color: #efefef;
    min-height: 100vh;
    height: 100%;
    margin-right: 128px;
    box-shadow: 0 3px 4px 0 rgba(0,0,0,0.14), 0 3px 3px -2px rgba(0,0,0,0.12), 0 1px 8px 0 rgba(0,0,0,0.20);
}

.custom-prism-param {
    color: #ffe100;
}

.highlight-overlay {
    transition: opacity 0.15s ease-in-out;
    opacity: 0;
    position: absolute;
    z-index: 10000;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(19, 19, 19, 0.55);
    overflow: hidden;
}


.highlight-addTableButton {
    z-index: 10001;
    pointer-events: none;
}

.highlight-addTableButton::after {
    display: flex;
    background: #ffffff5a;
    border-radius: 50%;
    height: 46px;
    width: 46px;
    animation: pulsate 1s ease-out;
    animation-iteration-count: infinite;
    opacity: 0.0;
    content: "";
    z-index: -1;
    position: absolute;
    top: 1px;
    left: 1px;
    right: 0;
    bottom: 0;
}

@keyframes pulsate {
    0% {
        transform: scale(0.1, 0.1);
        opacity: 0.0;
    }
    50% {
        opacity: 1.0;
    }
    100% {
        transform: scale(1.2, 1.2);
        opacity: 0.0;
    }
}


